@font-face {
  font-family: Roboto;
  src: url("./fonts/Roboto-Regular.ttf");
}
@font-face {
  font-family: Oswald;
  src: url("./fonts/Oswald-Regular.ttf");
}

body {
  margin: 0;
  padding: 0 1em;
  font-family: "Oswald";
  font-size: 22px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #1f1f1f;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
